.NaviLink{display:flex;align-items:center;justify-content:center;align-self:center;padding:5px 8px;width:70px;height:100%;font-weight:bold;text-decoration:none;color:white;text-shadow:1px 1px 1px black;font-size:20px}.NaviLink[data-selected='true']{padding:0;padding-right:8px;padding-left:8px;border-bottom:2px solid}.theme-light .NaviLink[data-selected='true']{border-color:#000}.theme-dark .NaviLink[data-selected='true']{border-color:#fffcfc}

.Toggle__Wrapper{display:flex;flex-flow:row;justify-items:center;align-items:center}.Toggle__Wrapper>label{color:white;text-shadow:1px 1px 1px black}.Toggle-checked{color:white !important}.theme-light .Toggle-track{background-color:#000 !important}.theme-dark .Toggle-track{background-color:#fff !important}

.Header__Wrapper{width:100vw;height:7vh;position:-webkit-sticky;position:sticky;border-bottom:1px solid rgba(0,0,0,0.2);display:grid;grid-template-columns:60% 10% 15% 15%}.theme-light .Header__Wrapper{background:#f56531}.theme-dark .Header__Wrapper{background:#24292e}.Header__Title{grid-column:1;margin-left:20px;font-size:20px;font-weight:bold;color:white;display:flex;align-self:center;text-shadow:1px 1px 1px black}.Header__Unit{grid-column:2;display:flex;justify-self:center;align-items:center}.Header__Theme{grid-column:3;display:flex;justify-self:center;align-items:center}.Header__Navigation{grid-column:4;display:flex;justify-content:space-evenly}

.TextSelect__Input{border:1px solid;border-radius:5px;outline:none;padding:0;font-family:"Assistant";box-shadow:none}.theme-light .TextSelect__Input{border-color:#f56531}.theme-dark .TextSelect__Input{border-color:#fff}.TextSelect__value-container{padding:0 !important}

.DayWeather__Wrapper{height:80%;width:8vw;display:grid;grid-template-rows:30% 40% 30%;border:1px solid;padding-left:10px;padding-right:10px;border-radius:5px;justify-items:center;align-items:center}.theme-light .DayWeather__Wrapper{border-color:#f56531}.theme-dark .DayWeather__Wrapper{border-color:#fff}.DayWeather__Day{font-weight:bold;font-size:25px}.DayWeather__Icon{height:5vh}.DayWeather__Temps{font-size:17px}.DayWeather__Temps>label:nth-of-type(2){padding:5px;font-weight:bold}

.CurrentWeatherValue__Wrapper{display:grid;grid-template-columns:50% 40%;grid-template-rows:50% 50%;justify-items:center;align-items:center}.CurrentWeatherValue__Wrapper>img{grid-row:1/3;grid-column:1;height:8vh}.CurrentWeatherValue__Wrapper>label{grid-column:2;justify-self:left;font-size:20px}.CurrentWeatherValue__Wrapper>label:nth-of-type(1){grid-row:1}.CurrentWeatherValue__Wrapper>label:nth-of-type(2){grid-row:2}

.CurrentWeather__Wrapper{display:grid;margin-top:5%;grid-template-rows:20% 75%;grid-template-columns:40% 40% 20%;height:100%}.CurrentWeather__Value{grid-row:1;grid-column:1}.CurrentWeather__Favorite{grid-row:1;grid-column:3;justify-self:center;align-self:center}.CurrentWeather__Text{grid-row:2;grid-column:1/4;justify-self:center;align-self:center;font-size:6vw;font-weight:600}

.withLoading__Spinner{justify-self:center}.theme-light .withLoading__Spinner{color:#f56531}.theme-dark .withLoading__Spinner{color:#fff}

.WeatherContainer__Wrapper{height:100%;width:100%;display:grid;grid-template-rows:65% 35%;border:1px solid;border-radius:5px}.theme-light .WeatherContainer__Wrapper{border-color:#f56531;color:#000}.theme-dark .WeatherContainer__Wrapper{border-color:#fff;color:#fff}.WeatherContainer__Current{grid-row:1}.WeatherContainer__Days{grid-row:2;display:flex;justify-content:space-evenly;align-items:center;justify-self:center;width:90%}

.HomePage__Wrapper{width:60%;height:85%;display:grid;grid-template-rows:20% 80%;align-content:center;align-items:center;justify-self:center}

.FavoriteWeatherData__Wrapper{height:100%;display:grid;grid-template-rows:20% 20% 40% 20%;justify-content:center;align-items:center;font-size:1.2rem;text-align:center}.theme-light .FavoriteWeatherData__Wrapper{color:#000;border-color:#f56531}.theme-dark .FavoriteWeatherData__Wrapper{color:#fff;border-color:#fff}.FavoriteWeatherData__Wrapper>label:first-of-type{font-size:1.3rem}.FavoriteWeatherData__Wrapper>img{height:6vw}.FavoriteWeatherData__Wrapper>label{cursor:pointer}

.FavoriteWeather__Wrapper{height:24vh;width:20vh;border:1px solid;border-radius:5px;display:flex;justify-content:center;align-items:center;cursor:pointer;-webkit-animation:shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;animation:shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;transform:translate3d(0, 0, 0)}.theme-light .FavoriteWeather__Wrapper{color:#000;border-color:#f56531}.theme-dark .FavoriteWeather__Wrapper{color:#fff;border-color:#fff}@-webkit-keyframes shake{10%,90%{transform:translate3d(-1px, 0, 0)}20%,80%{transform:translate3d(2px, 0, 0)}30%,50%,70%{transform:translate3d(-4px, 0, 0)}40%,60%{transform:translate3d(4px, 0, 0)}}@keyframes shake{10%,90%{transform:translate3d(-1px, 0, 0)}20%,80%{transform:translate3d(2px, 0, 0)}30%,50%,70%{transform:translate3d(-4px, 0, 0)}40%,60%{transform:translate3d(4px, 0, 0)}}

.FavoritesPage__Wrapper{height:80vh;width:70vw;display:grid;grid-template-columns:repeat(5, 1fr);grid-gap:30px;align-items:flex-start;justify-self:center;justify-items:center;overflow:scroll}

.AppRouter__Wrapper{display:grid;grid-template-rows:10vh 90vh;font-family:"Assistant"}.theme-dark{background-color:#4a4e4e}.theme-light{background-color:#fff}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

